.stats-bar {
  text-align: center;
  background-color: #00004d;
  border-top: 1px solid #f5f5f5;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 5px;
  padding-right: 5px;
  display: flex;
}

.stats-bar .group {
  flex-direction: row;
  flex-grow: 1;
  align-items: stretch;
  gap: 1px;
  display: flex;
}

.stats-bar .left {
  justify-content: flex-start;
}

.stats-bar .right {
  justify-content: flex-end;
}

.stats-bar .item {
  cursor: pointer;
  padding: 5px;
}

.stats-bar .item:hover {
  background-color: #0f0f99;
}

.stats-bar .text-highlight {
  color: #a5db94;
}

.stats-bar select {
  font: inherit;
  color: inherit;
  cursor: pointer;
  background-color: #00004d;
  border: none;
  outline: none;
}

#datasets-modal {
  min-height: 50vh;
  height: 50vh;
  flex-direction: column;
  display: flex;
}

#datasets-modal div {
  height: 60px;
  min-height: 60px;
  align-items: center;
  display: flex;
}

#datasets-list {
  border: 1px dotted #e0e0eb;
  flex: 1;
  padding: 5px;
  list-style: none inside;
  overflow-y: auto;
}

#datasets-list li {
  cursor: pointer;
  border-bottom: 1px solid #f5f5f5;
  flex-direction: row;
  align-items: center;
  padding: .5rem;
  font-family: monospace;
  display: flex;
}

#datasets-list li:hover {
  color: #00004d;
  background-color: #a5db94;
}

#datasets-list li:before {
  content: "";
  padding-right: .5rem;
  font-family: bootstrap-icons;
}

.custom-select {
  box-sizing: border-box;
  z-index: 99;
  max-height: 300px;
  background-color: #00004d;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
  display: flex;
  position: absolute;
  transform: translateY(5px);
  box-shadow: 1px 1px 2px #a5db94, -1px 1px 2px #a5db94;
}

.custom-select input {
  width: 100%;
  color: #a5db94;
  background-color: #0000;
  border: none;
  border-bottom: 1px solid #a5db94;
  outline: none;
  font-size: 1.1rem;
}

.custom-select ul {
  flex: auto;
  overflow-y: auto;
}

.custom-select ul li {
  cursor: pointer;
  padding: 5px;
}

.custom-select ul li:hover {
  color: #00004d;
  background-color: #a5db94;
}

.form {
  flex-direction: column;
  gap: 2rem;
  padding: 5px;
  display: flex;
}

.form input {
  font-size: 1.2rem;
}

.form button {
  align-self: flex-end;
}

.form .input {
  width: 100%;
  height: 100%;
  color: #a5db94;
  background-color: #0000;
  border: none;
  border-bottom: 1px solid #a5db94;
  outline: none;
}

.form .color-input {
  cursor: pointer;
  background-color: #0000;
  display: inline;
}

.form .slider {
  text-align: left;
  min-height: max-content;
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.form .slider label {
  color: #a5db94;
}

.form .slider .tip {
  color: #a5db94;
  justify-content: space-between;
  display: flex;
}

.class-form {
  text-align: left;
  font-size: 1.1rem;
}

.class-form span {
  flex-direction: column;
  display: flex;
}

.class-form .inline {
  flex-direction: row;
  align-items: center;
  gap: 15px;
  display: flex;
}

.class-form .inline button {
  width: 6rem;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  overflow: hidden;
}

body {
  height: 100vh;
  color: #f5f5f5;
  flex-direction: column;
  display: flex;
}

header {
  height: 60px;
  min-height: 60px;
  background-color: #00004d;
  border-bottom: 1px solid #000;
  flex-direction: row;
  align-items: center;
  display: flex;
}

header section {
  height: 100%;
  flex: 33%;
  align-items: center;
  gap: 10px;
  padding: 10px;
  display: flex;
}

header section:nth-child(2) {
  justify-content: center;
}

header section:nth-child(3) {
  justify-content: end;
}

main {
  height: 100%;
  flex-direction: row;
  display: flex;
}

aside, .image-list {
  min-width: 200px;
  background-color: #0f0f99;
  flex-direction: column;
  flex: 20%;
  display: flex;
  overflow-y: auto;
}

aside {
  border-left: 1px solid #00004d;
}

.display {
  background-color: #e0e0eb;
  flex: 80%;
  position: relative;
}

.drop-zone {
  height: 100%;
  width: 100%;
  z-index: 2;
  color: #38cb0b;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  background-color: #0f0f9980;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
}

.modal {
  width: 50vw;
  max-height: 80vh;
  height: -moz-fit-content;
  height: fit-content;
  min-width: 600px;
  text-align: justify;
  z-index: 3;
  color: #f5f5f5;
  background-color: #00004df2;
  border-radius: 3px;
  margin: auto;
  padding: 1em;
  font-size: 1.25em;
  display: block;
  position: absolute;
  inset: 0;
  overflow-y: auto;
  box-shadow: 0 0 10px #a5db94;
}

.modal h2 {
  text-align: center;
  color: #38cb0b;
  margin-bottom: 10px;
}

.modal p {
  margin-bottom: 15px;
}

.modal b {
  color: #38cb0b;
}

.modal u {
  color: #a5db94;
  text-decoration: none;
}

.btn {
  text-align: center;
  height: 40px;
  min-height: 40px;
  color: #f5f5f5;
  cursor: pointer;
  background-color: #0f0f99;
  border: 0;
  padding: .95rem;
  font-size: .9rem;
  text-decoration: none;
  box-shadow: 1px 1px 3px #e0e0eb;
}

.btn:hover {
  color: #00004d;
  background-color: #38cb0b;
}

.btn-icon {
  min-width: 0;
  width: auto;
  border-radius: 50%;
}

.btn-icon i {
  justify-content: center;
  align-items: center;
  display: flex;
}

.btn-selected {
  color: #00004d;
  background-color: #a5db94;
}

.btn-group {
  justify-content: center;
  align-items: center;
  display: flex;
  overflow: visible;
}

.btn-group .btn-small {
  height: auto;
  width: auto;
  min-height: auto;
  border: 0;
  padding: .6rem;
  font-size: .8rem;
}

.btn-group .btn-offside {
  background: radial-gradient(25% 100% at 0, #0000 50%, #e0e0eb 60%);
  border-top-right-radius: 20% 50%;
  border-bottom-right-radius: 20% 50%;
  padding: 2px 2px 2px 1rem;
  position: absolute;
  overflow: visible;
  transform: translateX(80%);
}

#canvas {
  outline: none;
}

#classes-wrapper {
  height: 40px;
